.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.grid-container {
  display: grid;
  grid-gap: 5px;
  
  padding: 5px;
}
.grid-container-table {
  display: grid;
  grid-gap: 5px;
  grid-template-columns: auto auto auto auto;
  
  padding: 5px;
}

.label{
  text-align: left;
  color: #5e5d5c;
  font-weight: 600;
  margin-top: 20px;
}
.grid-item {
  width:100%;
  height:35px;
  padding: 2px;
  font-size: 16px;
 
}
.panel {
  text-align: left;
  font-size: 12px;
  color: red;
  margin-left: 5px;
}